
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapActions } from 'vuex'
export default {
name: 'Register',
components: {
Group,
Cell,
XInput,
XButton
},
data() {
return {
isSubmitting: false,
username: '',
email: '',
password: '',
password2: '',
};
},
methods: {
checkData() {
if (this.username == '') {
this.$vux.toast.show({
text: '',
type: 'warn',
});
return false;
}
if (this.email == '') {
this.$vux.toast.show({
text: '',
type: 'warn',
});
return false;
}
if (this.password == '') {
this.$vux.toast.show({
text: '',
type: 'warn',
});
return false;
}
if (this.password2 == '') {
this.$vux.toast.show({
text: '',
type: 'warn',
});
return false;
}
return true;
},
onSubmit() {
if (this.checkData()) {
this.register({
data: {
username: this.username,
password: this.password,
email: this.email
},
cb: (res) => {
if (!res.success) {
this.$vux.toast.show({
text: res.error,
type: 'warn',
});
}
else{
this.$vux.toast.show({
text: '',
type: 'success',
});
this.$router.push({
name: 'Login'
});
}
}
});
}
},
...mapActions(['register'])
}
};




